import React from 'react'
import tarang from '../img/sch.jpg'
class Dashboard extends React.Component{
    render(){
        return(
            <div>
                <div style ={{margin : '2%'}}>
                    <article className ='tile is-mobile is-child notification' >
                        <img src = {tarang} />
                    </article>
                </div>
            </div>
        )
    }
}
export default Dashboard


